import React, { useState } from 'react';
import { fileToBase64 } from '../../assets/common/file-to-base64';
import { ApiPath } from '../../assets/common/base-url';

export const Lifestyle = (props) => {

    const [showDialog, setShowDialog] = useState(false);

    return (
        <div className="tab-page-content">
            <div className="page-header">
                <h2>Medical History</h2>
            </div>
            <div className="page-body">
                <table className="profile-table">
                    <tbody>
                        <tr>
                            <td>Current Medication</td>
                            <td>[File...]</td>
                            <td onClick={() => setShowDialog(true)}><i className="icofont-ui-edit"></i> edit</td>
                        </tr>
                        <tr>
                            <td>Past prescriptions</td>
                            <td>[File...]</td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>Diagnosis Report</td>
                            <td>[File...]</td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <LifestyleDialog
                showDialog={showDialog}
                hideDialog={() => setShowDialog(false)}
                setIsLoaderVisible={props.setIsLoaderVisible}
                showToast={props.showToast}
                data={props.data}
                setResetData={props.setResetData}
            />
        </div>
    );
}

const LifestyleDialog = props => {
    const [txtCurrentMedications, setTxtCurrentMedications] = useState('')
    const [txtPastPrescriptions, setTxtPastPrescriptions] = useState('')
    const [txtDiagnosisReport, setTxtDiagnosisReport] = useState('')

    const updateLifestyleDetails = () => {
        const data = {
            currentMedications: [txtCurrentMedications],
            pastPrescriptions: [txtPastPrescriptions],
            diagnosisReport: [txtDiagnosisReport],

        }

        const options = {
            'body': JSON.stringify(data),
            'headers': {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${sessionStorage.getItem("token")}`
            },
            'method': "PUT",
        }

        props.setIsLoaderVisible(true)

        //Then update just this data...
        fetch(ApiPath + "patient/profileUpdate", options)
            ?.then(response => {
                props.setIsLoaderVisible(false);
                return response.json();
            })
            .then(response => {
                if (response && response.statusCode === 200) {
                    props.showToast("Update successful!", 'success');
                    props.hideDialog();

                    //Remember to refresh the fetched data after this..
                    props.setResetData(true);

                } else {
                    props.showToast(response.message, 'exclamation');
                }
            })
            .catch(error => {
                props.setIsLoaderVisible(false);
                props.showToast(error.message, 'exclamation');
            })
    }

    const saveImageToBase64 = (val) => {

        let fileUpload;

        if (val === "curr") {
            fileUpload = document.querySelector("#txtCurrentMedications")
        } else if (val === "pp") {
            fileUpload = document.querySelector("#txtPastPrescriptions")
        } else {
            fileUpload = document.querySelector("#txtDiagnosisReport")
        }

        if (fileUpload?.files[0]?.size > 500152) {
            props.showToast("File size cannot be more than 500kb!", "exclamation")
            fileUpload.files = null;
            return;
        }

        if (fileUpload?.files[0]) {
            //Ensuring that there is a file to convert
            fileToBase64(fileUpload?.files[0])
                .then(response => {
                    switch (val) {
                        case "curr":
                            setTxtCurrentMedications(response.toString());
                            break;
                        case "pp":
                            setTxtPastPrescriptions(response.toString());
                            break;
                        default:
                            setTxtDiagnosisReport(response.toString());
                            break;
                    }
                })
                .catch(err => {
                    props.showToast(err);
                })

        } else {
            setTxtCurrentMedications("");
        }
    }

    const displayDialog = props.showDialog ? "dialog-background fade" : "dialog-background";

    return (
        <div className={displayDialog}>
            <div className="dialog-container">
                <div className="dialog-header">
                    <h2 className="nully main"><i className="icofont-ui-edit"></i> Edit Medical History</h2>
                </div>
                <div className="dialog-body main">

                    <div className="form-row-2-1">
                        <div className='input-group'>
                            <label>Current Medications</label>
                            <input
                                id="txtCurrentMedications"
                                onChange={() => saveImageToBase64("curr")}
                                className="form-control"
                                accept="image/*, .pdf, .doc, .docx"
                                type="file"
                            />

                        </div>
                        <div className="input-group" style={{ marginTop: 40 }}>
                            <button
                                id="btnCurrentMedication"
                                className="btn-main"
                                onClick={() => window.alert("Displays a preview of the saved document. To be conpleted later...")}>
                                Preview Saved...
                            </button>
                        </div>

                    </div>
                    <div className="form-row-2-1">
                        <div className='input-group'>
                            <label>Past Prescriptions</label>
                            <input
                                id="txtPastPrescriptions"
                                onChange={() => saveImageToBase64("pp")}
                                className="form-control"
                                accept="image/*, .pdf, .doc, .docx"
                                type="file"
                            />

                        </div>
                        <div className="input-group" style={{ marginTop: 40 }}>
                            <button
                                id="btnPastPrescriptions"
                                className="btn-main"
                                onClick={() => window.alert("Displays a preview of the saved document. To be conpleted later...")}>
                                Preview Saved...
                            </button>
                        </div>

                    </div>
                    <div className="form-row-2-1">
                        <div className='input-group'>
                            <label>Diagnosis Report</label>
                            <input
                                id="txtDiagnosisReport"
                                onChange={() => saveImageToBase64("diag")}
                                className="form-control"
                                accept="image/*, .pdf, .doc, .docx"
                                type="file"
                            />

                        </div>
                        <div className="input-group" style={{ marginTop: 40 }}>
                            <button
                                id="btnPastPrescriptions"
                                className="btn-main"
                                onClick={() => window.alert("Displays a preview of the saved document. To be conpleted later...")}>
                                Preview Saved...
                            </button>
                        </div>
                    </div>
                </div>
                <div className="dialog-footer main">
                    <button
                        id="btnUpdate"
                        className="btn-main mr-1"
                        onClick={updateLifestyleDetails}
                    >Update
                    </button>
                    <button
                        id="btnCancel"
                        className="btn-main-outline"
                        onClick={() => props.hideDialog()}
                    >Cancel
                    </button>
                </div>
            </div>
        </div>
    )
}
